/* contact.css */


@font-face {
    font-family: 'Alegreya SC';
    /* src: url('../fonts/AlegreyaSC-Medium.ttf'); */
    src: url('../fonts/AlegreyaSC-Bold.ttf');
    font-weight: medium;
    font-style: normal;
}

:root {
    --ffff-ffff: rgb(1, 1, 1);
}

* {
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

html, body {
    margin: 0;
    padding: 0;
    font-size: 16px;
    background: #e0f2fe;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

/* MAIN/WRAP */
#about-main-body {
    display: flex;
    flex-direction: column;
}

/* ABOUT TOP LANDING */
#landing-wrap {
    /* background-image: url('../images/contact-img3.png'); */
    background-image: url('../images/top_background.jpeg');
    background-size: cover; /* or contain? */
    background-repeat: no-repeat;
    background-position: right;
    
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    height: 80vh;
    width: 100%;
    z-index: 0;
    max-height: 900px;
}

.landing-overlay {
    position: absolute;
    height: 80vh;
    max-height: 900px;
    width: 100%;
    /* background: rgba(50, 36, 86, 0.3); */
    background: rgba(135, 121, 171, 0.3);
    backdrop-filter: blur(7px);
    z-index: 1;

}

.about-top-sub-cont {
    max-width: 500px;
    text-align: center;
    display: flex;
    flex-direction: column;
    padding-top: 50px;
    z-index: 3;
}

.about-top-sub-cont > h1 {
    /* color: white; */
    color: rgba(1, 36, 86, 1);
    /* font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif; */
    font-family: 'Alegreya SC';
    font-size: 4rem;
    padding-top: 10px;
    padding-bottom: 10px;
    margin: 0;
    /* border: 1px solid red; */
}

.about-top-sub-cont > h3 {
    /* color: rgba(219, 153, 36, 1); */
    /* color: rgba(234, 179, 8, .9); */
    /* color: rgba(207, 93, 23, .9); */
    /* color: rgba(1, 36, 86, 1); */
    /* color: rgba(219, 153, 36, 1); */
    /* color: rgba(14, 165, 233, .7); */
    color: rgba(219, 153, 36, .9);
    font-family: Arial, Helvetica, sans-serif;
    font-size: 1.7rem;
}


/* HEADER */
.top-main {
    position: absolute;
    width: 100vw;
    height: auto;
    padding-top: 10px;

    display: flex;
    flex-direction: row;
    justify-content: center;
    z-index: 3;
    /* background-color: rgba(219, 153, 36, .3); */
}

.header-links {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    box-sizing: border-box;
    gap: 5px;

    list-style-type: none;
    margin: 0;
    padding: 0;
}

.head-link-item {
    display: flex;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
   
    padding: 8px 8px;
    margin: 0;
    height: min-content;
    cursor: pointer;
    /* border: 1px solid black; */
    /* links should be dark blue, with a yellow hover. font should be mild bold. */
};

.head-logo {
    text-decoration: none;
    color: inherit;
}

.head-link-item > a,
#nav-srvc-menu a {
    box-sizing: border-box;
    text-decoration: none;
 }

.head-link-item > .unselected,
.head-link-item.unselected {
    color: rgba(1, 36, 86, 1);
    font-size: 22px;
    font-weight: 800;
}

.head-link-item > .unselected:hover,
.head-link-item > .unselected:active {
    color: rgba(219, 153, 36, 1);
}

.head-logo {
    background-image: url('../images/rwags-logo.png');
    background-size: contain; /* or contain? */
    background-repeat: no-repeat;
    position: relative;
    width: 180px;
    height: 120px;
}

.head-logo a {
    position: absolute;  /* Positioning the link within the container */
    top: 0;
    left: 0;
    width: 100%; /* Ensures the link covers the full container */
    height: 100%;
    z-index: 10;
}


/* HEADER: SELECTED PAGE LINK */
.selected-link {
    color: rgba(219, 153, 36, 1);
    font-size: 23px;
    font-weight: 800;
    /* border-bottom: 3px solid rgba(14, 165, 233, .4); */
}


/* DROPDOWN MENU */
#dropdown-menu {
    display: none;
    flex-direction: column;
    position: absolute;
    box-sizing: border-box;
    width: 200px;
    height: auto;
    /* background: rgba(14, 165, 233, .8); */
    background: rgba(1, 36, 86, .9);
    /* border: 1px solid rgba(219, 153, 36, 1); */
    /* rgba(2, 132, 199, */
    padding: 5px;
    padding-top: 10px;
    padding-bottom: 10px;
    /* transition: opacity 0.4s ease, transform 0.4s ease; /* Transition effects */
    /* transform: translateY(-10px); Initial transform state */
    /* pointer-events: none; */
    z-index: 2;
}

#dropdown-menu > a {
    box-sizing: border-box;
    text-decoration: none;
    padding: 7px;
    color: white;
    z-index: 10;
}

#dropdown-menu > a:hover {
    box-sizing: border-box;
    text-decoration: none;
    color: rgba(219, 153, 36, 1);
}

#dropdown-menu span {
    box-sizing: border-box;
    padding-top: 10px;
    padding-bottom: 10px;
    font-size: 18px;
}

/* MOBILE MENU */
.mobile-header {
    display: none;
    flex-direction: row;
}

.mobile-btn-wrap {
    position: fixed;
    top: 15px;
    left: 15px;

    box-sizing: border-box;
    padding: 0;
    margin: 0;
    z-index: 20;
}

#mobile-nav-btn {
    display: inline-flex;
    align-items: center;  
    justify-content: center;
    padding: 0; 
    width: 35px; 
    height: 35px; 
    border: 1px solid black;
    border-radius: 5px;
    background: rgba(14, 165, 233, .9);
    fill: rgba(234, 179, 8, 1);
    cursor: pointer;
}


/* nav overlay dialog menu */
#mobile-dialog {
    display: none;
    position: fixed; /* Position fixed to cover the whole screen  */
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100vw;
    height: 100vh;
    background: rgba(1, 36, 86, 0.4);
    backdrop-filter: blur(7px);
    z-index: 1000;
    border: none;
    outline: none;
}

#mobile-dialog > nav {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;
    width: 100%;
    /* border: 1px solid red; */
    position: relative;
}

.nav-cls-btn-wrp {
    position: absolute;
    box-sizing: border-box;
    top: 10px;
    right: 10px;
    display: flex;
    justify-content: end;
    /* border: 1px solid pink; */
    /* padding: 3px; */
    width: 100%;
}

.dialog-lnks-wrap {
    /* border: 1px solid black; */
    min-height: 300px;
}

.dialog-lnks-wrap > ul {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    list-style-type: none;
    margin: 0;
    padding: 0;
}

.nav-close-btn {
    display: inline-flex; /* Use flex to center SVG */
    align-items: center;   /* Center vertically */
    justify-content: center; /* Center horizontally */
    padding: 0; /* Remove default padding */
    width: 35px; /* Set desired width */
    height: 35px; /* Set desired height */
    border: 1px solid white;
    border-radius: 5px;
    fill: white;
    background: rgba(1, 1, 1, .4);
    cursor: pointer; /* Change cursor to pointer */
}

.dialog-lnks-wrap .head-link-item > .unselected,
.dialog-lnks-wrap .head-link-item.unselected {
    /* color: rgba(1, 36, 86, 1); */
    color: white;
    font-size: 28px;
    cursor: pointer;
}

.dialog-lnks-wrap .head-link-item > .unselected:hover,
.dialog-lnks-wrap .head-link-item > .unselected:active,
#nav-srvc-menu .unselected:active {
    color: rgba(219, 153, 36, 1);
    font-size: 28px;
}

.dialog-lnks-wrap .head-link-item > .selected-link {
    color: rgba(219, 153, 36, 1);
    font-size: 28px;
    background: inherit;
}

#nav-srvc-menu {
    display: none;
    width: 70%;
    flex-direction: column;
    align-items: center;
    gap: 4px;
    padding-top: 7px;
    padding-bottom: 7px;
    /* border: 1px solid black; */
}

#nav-srvc-menu a {
    color: rgba(219, 153, 36, 1);
    /* background: rgba(1, 36, 86, .3); */
    background: rgba(14, 165, 233, .4);
    border-radius: 5px;
    padding: 4px 10px;
}


/* CONTACT INFO AREA */

.contact-info-wrap {
    display: flex;
}

section > .contact-info-wrap {
    display: flex;
    flex-direction: column;
    /* justify-content: center; */
    align-items: center;

    /* border: 1px solid green; */
    /* min-height: 400px; */
    /* padding: 15px; */
    padding-bottom: 10px;
    padding-left: 10px;
    padding-right: 10px;
    
}

.info-subwrap {
    min-width: 91.5%;
    max-width: 700px;
}

.cont-into-title {
    text-align: center;
    font-size: 26px;
    letter-spacing: .1rem;
    /* height: 50px; */
    color: rgba(1, 36, 86, 1);
    /* border: 1px solid green; */
}

/* upper divider */
.divider-wrap {
    display: flex;
    justify-content: center;
    align-items: center;

    box-sizing: border-box;
    height: 35px;
    min-width: 100%;
    /* border: 1px solid black; */
}

.divider-spn {
    width: 75%;
    height: 2.5px;
    background: #0369a1;
}

.contact-subwrap {
    display: flex;
    justify-content: center;
    box-sizing: border-box;
    min-width: 91.5%;
    max-width: 700px;
    /* border-radius: 5px; */
    /* border: 1px solid black; */
    /* background: #0369a1; */
    background: rgba(14, 165, 233, .5);
    border-left: 4px solid rgba(14, 165, 233, .9);
    padding: 15px;

}

.adr-link-wrap > a {
    color: inherit;
    text-decoration: underline solid white;
}

.cont-into-box {
    padding: 10px;
}

.cont-into-box > h3 {
    margin: 0;
    padding-bottom: 12px;
    /* color: rgba(219, 153, 36, 1); */
    /* color: white; */
    /* color: rgba(14, 165, 233, 1); */
    color: rgba(219, 153, 36, 1);

    font-size: 21px;
    width: max-content;
    -webkit-font-smoothing: antialiased;
}

.email-num-cont {
    display: flex;
    flex-direction: column;
    gap: 2px;
}

.cont-into-box p {
    padding: 0;
    margin: 0;
    /* color: #0369a1; */
    /* color: rgba(219, 153, 36, 1); */
    color: rgba(16, 28, 86, 1);
    font-size: 1rem;
    font-weight: 500;
}

.email-num-cont > p {
    /* border: 1px solid black; */
    border-radius: 5px;
    padding: 6px 3px;
    width: max-content;
}

.email-num-cont > p:hover,
.email-num-cont > p:active {
    border-radius: 5px;
    /* background: #0369a1;
    color: rgba(219, 153, 36, 1); */
    background: rgba(219, 153, 36, 1);
    color:rgba(16, 28, 86, 1);
    /* padding: 6px 4px; */
}

.email-num-cont a {
    text-decoration: none; /* Removes the underline */
    color: inherit;
    height: 100%;
    width: 100%;
    /* border: 1px solid black;
    border-radius: 5px; */
    /* padding: 5px 5px; */
}

/* divider */
.divider-wrap-lower {
    display: flex;
    justify-content: center;
    align-items: center;

    box-sizing: border-box;
    width: 5%;
    /* border: 1px solid black; */
}

.divider-spn-lower {
    width: 2.5px;
    height: 60%;
    /* background: rgba(1, 36, 86, 0.94); */
    background: white;
}


/* MAP SECTION */
.map-wrap {
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    /* padding-left: 10px;
    padding-right: 10px; */
    padding-top: 10px;
    padding-bottom: 5px;
    min-width: 91.5%;
}

#goog-map {
    width: 100%;
    min-height: 340px;
    box-sizing: border-box;
    border: none;
    border-left: 4px solid rgba(14, 165, 233, .9)
}


/* Form */
.form-wrap {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* border: 1px solid black; */
    padding-bottom: 50px;
    padding-top: 30px;
    gap: 5px;
}

.form-section-intro {
    color: #0369a1;
    text-align: center;
    box-sizing: border-box;
    width: 88%;
}

.form-section-title {
    box-sizing: border-box;
    width: 88%;
    margin: 0;
    /* padding: 8px;
    padding-top: 20px; */
    font-size: 1.3rem;
    text-align: center;
    /* border: 1px solid green; */
    
}

#contact-form {
    width: 85%;
    /* border: 1px solid black; */
    border-left: 4px solid rgba(14, 165, 233, .9);
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    padding: 5px;
    padding-top: 0px;
    padding-bottom: 0px;
    display: flex;
    flex-direction: column;
    gap: 3px;
}

/* .sub-input-group {
    display: flex;
    flex-direction: column;
    border: 1px solid yellow;
    gap: 2px;
} */

.form-input-field {
    box-sizing: border-box;
    border: 1px solid black;
    padding: 7px;
    /* background-color: #fff; */
    border: 1px solid #ccc;
    border-radius: 4px;
    box-shadow: inset 0 1px 1px rgba(0,0,0,.075);
    transition: border-color ease-in-out .15s,box-shadow ease-in-out .15s,-webkit-box-shadow ease-in-out .15s;
}

.form-input-field:focus {
    border-color: #66afe9;
    outline: 0;
    box-shadow: inset 0 1px 1px rgba(0,0,0,.075),0 0 8px rgba(102,175,233,.6);
}

.name-group {
    display: flex;
    flex-direction: row;
    border: 1px solid green;
    gap: 8px;
}

#form-submit {
    all: unset;
    text-align: center;
    border: 1px solid black;
    border-radius: 3px;
    padding: 7px 30px;
    color: rgba(232, 123, 11, 1);
    background: rgba(33, 80, 173, .9);
    width: 15%;
}

#form-submit:hover {
    background: rgba(16, 28, 86, .9);
    /* background: rgba(33, 80, 173, 1); */
    /* border: 1px solid rgba(232, 123, 11, 1); */
}

textarea.form-input-field {
    resize: vertical;
    min-height: 100px;
    max-width: 100%;
}

.form-label {
    font-size: 1rem;
}

.form-req-span {
    color: red;
}


/* FOOTER SECTION */

.index-footer {
    min-height: 400px;
    display: flex;
    justify-content: center;
    /* background: rgba(2, 6, 23, .8); */
    /* background: rgba(217, 119, 6, .7); */
    background: rgba(14, 165, 233, .9);
}
 
.logo-foot-wrap {
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding-right: 20px;
    padding-left: 20px;
    width: 100%;
}

.foot-contact-left {
    box-sizing: border-box;
    padding-left: 10px;
    padding-right: 10px;
    max-width: 400px;
}

.foot-contact-left > span {
    font-size: .6rem;
}
 
.footer-logo {
    background-image: url('../images/rwags-logo.png');
    background-size: cover; /* or cover? */
    background-repeat: no-repeat;
    background-color: rgba(234, 179, 8, .9);

    display: block;
    aspect-ratio: 1.53 / 1;
    width: 90%;
    max-width: 400px;
    height: auto;
    border: 1px solid rgba(234, 179, 8, .9);
    border-radius: 5px;
    object-fit: contain;
}

.logo-foot-wrap > div > h2 {
    color: rgba(240, 233, 221, 1);
}

.foot-links {
    display: flex;
    gap: 12px;
    list-style-type: none;
    margin: 0;
    padding: 0;
}

#phone-number {
    font-size: 2.5rem;
}

#phone-number > a {
    text-decoration: none; /* Removes the underline */
    color: inherit;
}

#phone-number > a:hover {
    color: rgba(219, 153, 36, .8);
}

.foot-link > a {
    text-decoration: none; /* Removes the underline */
    color: inherit;
    /* color: rgba(1, 36, 86, 0.94); */
    color: rgba(219, 153, 36, 1);
    font-size: 1rem;
}



/* works for far */
@media screen and (width < 741px) {

#landing-wrap {
    justify-content: end;
}

.about-top-sub-cont {
    max-width: 500px;
    height: 65%;
}

.contact-subwrap {
    min-width: 100%;
}

/* map */
.map-wrap {
    min-width: 100%
}


/* form */
.form-label {
    font-size: .9rem;
}

/* footer */
.logo-foot-wrap {
    flex-direction: column;
    padding-top: 20px;
    padding-bottom: 30px;
}

.foot-contact-left {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-bottom: 30px;
}

#phone-number {
    margin: 0;
    padding: 5px;
}

#phone-number > a:active {
    color: rgba(219, 153, 36, 1);
}

.foot-links {
    padding-top: 5px;
    padding-bottom: 5px;
}

.footer-logo {
    display: block;
    aspect-ratio: 1.53 / 1;
    width: 90%;
    max-width: 400px;
    height: auto;
    border: 1px solid rgba(234, 179, 8, .9);
    border-radius: 5px;
    object-fit: contain;
}

    /* non-mobile-header */
    .top-main {
        display: none;
    }

 /* MOBILE NAV MENU */
    .mobile-header {
        position: absolute;
        top: 0;
        padding-top: 20px;
        display: flex;
        flex-direction: row;
        justify-content: center;
        width: 100%;
        z-index: 4;
    }

    .head-logo {
        width: 200px;
        height: 130px;
    }
}


@media screen and (width < 650px) {
/* contact section */
    .contact-subwrap {
        flex-direction: column;
        align-items: start;
        padding-left: 8px;
        padding-right: 8px;
    }



    /* dividers for contact */
    .divider-wrap-lower {
        justify-content: center;
        width: 100%;
        height: 20px;
        /* border: 1px solid black; */
    } 

    .divider-spn-lower {
        width: 45%;
        height: 2.5px;
        /* background: rgba(1, 36, 86, 0.94); */
    }

    .email-num-cont > p {
        padding: 6px 0px;
    }
}


@media screen and (width < 401px) { 
    /* section > .about-cont-wrap {
        padding-right: .5rem;
        padding-left: .5rem;
        border: 1px solid red;
    } */
/* 
    section > .contact-info-wrap {
        width: 90%;
    } */
    /* .contact-subwrap {
        width: 98%;
    } */


    /* map */
    /* .map-wrap {
        padding-left: 8px;
        padding-right: 8px;
    }

    #goog-map {
        width: 98%;
    } */

    /* form */
    .form-label {
        font-size: .8rem;
    }

    /* footer  */
    .foot-link > a {
        font-size: .8rem;
    }

    #phone-number {
        font-size: 2.1rem;
    }

    .foot-links {
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        gap: 9px;
    }
}

